import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";

import notification from "../../../../components/notification";
import { Empty, Input, Collapse, Select, Tooltip } from "antd";

import Popconfirms from "../../../../components/feedback/popconfirm";
// import Box from '../../components/utility/box';
// import HelperText from "../../../../components/utility/helper-text";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/cashBank/pettyCashType/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  StatusTag,
  customCollapseStyle
} from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";

const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;

const redirectTo = "/dashboard/setup/menus";

class PettyCashType extends Component {
  state = {
    filter: {
      id_account: null,
      transaction_type: null,
      pagination: null
    },
    search: null,
    transaction_types: [
      {
        id: 1,
        name: getLanguage({ id: "deposit" })
      },
      {
        id: 2,
        name: "Withdrawal"
      }
    ]
  };

  componentDidUpdate() {
    // Display error notif
    if (this.props.pettyCashType.error) {
      notification("error", this.props.pettyCashType.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.pettyCashType.success) {
      notification("success", this.props.pettyCashType.success);
      this.props.clearSuccess();
    }
  }

  // Get All data diawal
  componentDidMount() {
    const { getPettyCashTypes } = this.props;
    getPettyCashTypes();
  }

  // Handle search
  handleSearch = value => {
    const { getPettyCashTypes } = this.props;
    this.setState({
      filter: {
        id_account: null,
        transaction_type: null,
        pagination: null
      }
    });

    getPettyCashTypes(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;

    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getPettyCashTypes } = this.props;
    this.setState({ search: null });
    this.setState({
      filter: {
        id_account: [],
        transaction_type: [],
        pagination: null
      }
    });
    getPettyCashTypes();
  };

  // Handle filter change
  handleFilterChange = (name, value) => {
    this.setState({
      filter: {
        ...this.state.filter,
        [name]: value
      }
    });
  };

  // Handle filter
  handleFilter = () => {
    const { getPettyCashTypes } = this.props;
    //const { current_page } = this.props.pettyCashType;
    const { filter } = this.state;
    let string = "";

    if (filter.id_account) {
      string += "id_account=" + filter.id_account;
    }
    if (filter.transaction_type) {
      string
        ? (string += "&transaction_type=" + filter.transaction_type)
        : (string += "transaction_type=" + filter.transaction_type);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }
    this.setState({ search: null });
    getPettyCashTypes(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getPettyCashTypes } = this.props;
    this.setState({
      filter: {
        id_account: null,
        transaction_type: null,
        pagination: null
      }
    });
    this.setState({ search: null });
    getPettyCashTypes();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activatePettyCashType(id);
    } else {
      this.props.deactivatePettyCashType(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getPettyCashTypes } = this.props;
    const { search } = this.props.pettyCashType;
    const { filter } = this.props.pettyCashType;
    getPettyCashTypes(page, search, filter);
  };

  listAction = {
    title: "",
    dataIndex: "id_petty_cash_type",
    rowKey: "id_petty_cash_type",
    width: "10%",
    align: "center",
    render: (text, row) => (
      <div>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button size="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>

        {row.is_active ? (
          <Popconfirms
            title={<IntlMessages id="pettyCashTypes.popconfirm.deactivate" />}
            onConfirm={this.handleRecord.bind(this, "deactivate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.deactivate" />}
            >
              <Button size="small" className="invoiceDltBtn">
                <i className="ion-close-circled" />
              </Button>
            </Tooltip>
          </Popconfirms>
        ) : (
          <Popconfirms
            title={<IntlMessages id="pettyCashTypes.popconfirm.activate" />}
            onConfirm={this.handleRecord.bind(this, "activate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.activate" />}
            >
              <Button size="small" className="invoiceRstBtn">
                <i className="ion-refresh" />
              </Button>
            </Tooltip>
          </Popconfirms>
        )}
      </div>
    )
  };

  render() {
    const columns = [
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "petty_cash_type_code",
        rowKey: "petty_cash_type_code",
        width: "10%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "name" }),
        dataIndex: "description",
        rowKey: "description",
        width: "15%",
        render: display => {
          return display.length > 50
            ? display.substring(0, 50) + "..."
            : display;
        }
      },
      {
        title: getLanguage({ id: "items.tab.account" }),
        dataIndex: "account",
        rowKey: "account",
        width: "10%",
        render: (text, row) => {
          return (
            <span>{text.account_code + " (" + text.account_name + ")"}</span>
          );
        }
      },
      {
        title: getLanguage({ id: "transactionType" }),
        dataIndex: "transaction_type",
        rowKey: "transaction_type",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          if (text === 1) {
            className = "primary";
            display = getLanguage({ id: "deposit" });
          } else {
            className = "active";
            display = "Withdrawal";
          }
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "is_active",
        rowKey: "is_active",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          if (text === 1) {
            className = "active";
            display = getLanguage({ id: "active" });
          } else {
            className = "nonactive";
            display = getLanguage({ id: "inactive" });
          }
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];
    // console.log(this.state.filter);

    // Data setup
    const { petty_cash_types } = this.props.pettyCashType;
    //console.log(this.props.pettyCashType); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    if (
      permissions.includes("Update Petty Cash Type") &&
      columns.length === 5
    ) {
      columns.push(this.listAction);
    }

    Object.keys(petty_cash_types).map((pettyCashType, index) => {
      return dataSource.push({
        ...petty_cash_types[pettyCashType],
        key: pettyCashType
      });
    });

    // Pagination Setup
    const { current_page, page_size, total } = this.props.pettyCashType;

    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="pettyCashTypes.pageHeader" />}
            marginBottom={true}
          >
            <div style={{ display: "flex", justifyContent: "flex-end" }}>
              {permissions.includes("Create Petty Cash Type") ? (
                <Link to={`${this.props.match.path}/add`}>
                  <Button type="primary" className="mateAddInvoiceBtn">
                    <IntlMessages id="pettyCashTypes.button.add" />
                  </Button>
                </Link>
              ) : null}
              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={
                getLanguage({ id: "code" }) + ", " + getLanguage({ id: "name" })
              }
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <div className="isoFilterContent">
                  <div style={{ width: "50%" }}>
                    <label>
                      <IntlMessages id="accounts.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_account"
                      )}
                      value={
                        this.state.filter.id_account
                          ? this.state.filter.id_account
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="pettyCashTypes.filter.select.account" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={this.props.pettyCashType.loading}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {this.props.pettyCashType.accounts.map((data, key) => (
                        <Option key={key} value={data.id_account}>
                          {data.account_code + " (" + data.account_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  </div>
                  <div style={{ width: "20%" }}>
                    <label>
                      <IntlMessages id="transactionType" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "transaction_type"
                      )}
                      value={
                        this.state.filter.transaction_type
                          ? this.state.filter.transaction_type
                          : []
                      }
                      style={{ width: "90%" }}
                      placeholder={
                        <IntlMessages id="pettyCashTypes.filter.select.transactionType" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {this.state.transaction_types.map((data, key) => (
                        <Option key={key} value={data.id}>
                          {data.name}
                        </Option>
                      ))}
                    </Select>
                  </div>

                  <div style={{ width: "15%" }}>
                    <label>
                      <IntlMessages id="dataPerPage" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "pagination"
                      )}
                      value={
                        this.state.filter.pagination
                          ? this.state.filter.pagination
                          : page_size
                      }
                      style={{ width: "90%" }}
                      placeholder={
                        <IntlMessages id="pettyCashTypes.filter.select.transactionType" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={this.props.pettyCashType.loading}
                      //allowClear={true}
                      //showArrow={false}
                    >
                      <Option key={1} value={10}>
                        10
                      </Option>
                      <Option key={2} value={25}>
                        25
                      </Option>
                      <Option key={3} value={50}>
                        50
                      </Option>
                      <Option key={4} value={100}>
                        100
                      </Option>
                    </Select>
                  </div>

                  <div
                    style={{
                      width: "20%",
                      marginTop: "auto"
                    }}
                  >
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                    >
                      <IntlMessages id="filter.submit" />
                    </Button>
                    <Button onClick={this.handleResetFilter} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </div>
                </div>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {petty_cash_types.length === 0 &&
            !this.props.pettyCashType.loading ? (
              <Empty
                description={<IntlMessages id="pettyCashTypes.table.noData" />}
              />
            ) : (
              <div className="isoInvoiceTable">
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={this.props.pettyCashType.loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={
                      page_size
                        ? {
                            current: current_page,
                            pageSize: parseInt(page_size),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                        : true
                    }
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    pettyCashType: state.pettyCashType,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(PettyCashType);
