const actions = {
  GET_PETTY_CASH_TYPES_REQUEST: "GET_PETTY_CASH_TYPES_REQUEST",
  GET_PETTY_CASH_TYPES_SUCCESS: "GET_PETTY_CASH_TYPES_SUCCESS",
  GET_ACCOUNTS_SUCCESS: "GET_ACCOUNTS_SUCCESS",
  CREATE_PETTY_CASH_TYPE_REQUEST: "CREATE_PETTY_CASH_TYPE_REQUEST",
  STORE_PETTY_CASH_TYPE_REQUEST: "STORE_PETTY_CASH_TYPE_REQUEST",
  STORE_PETTY_CASH_TYPE_SUCCESS: "STORE_PETTY_CASH_TYPE_SUCCESS",
  STORE_PETTY_CASH_TYPE_ERROR: "STORE_PETTY_CASH_TYPE_ERROR",
  EDIT_PETTY_CASH_TYPE_REQUEST: "EDIT_PETTY_CASH_TYPE_REQUEST",
  EDIT_PETTY_CASH_TYPE_SUCCESS: "EDIT_PETTY_CASH_TYPE_SUCCESS",
  UPDATE_PETTY_CASH_TYPE_REQUEST: "UPDATE_PETTY_CASH_TYPE_REQUEST",
  UPDATE_PETTY_CASH_TYPE_SUCCESS: "UPDATE_PETTY_CASH_TYPE_SUCCESS",
  UPDATE_PETTY_CASH_TYPE_ERROR: "UPDATE_PETTY_CASH_TYPE_ERROR",
  SHOW_PETTY_CASH_TYPE_REQUEST: "SHOW_PETTY_CASH_TYPE_REQUEST",
  SHOW_PETTY_CASH_TYPE_SUCCESS: "SHOW_PETTY_CASH_TYPE_SUCCESS",
  DEACTIVATE_PETTY_CASH_TYPE_REQUEST: "DEACTIVATE_PETTY_CASH_TYPE_REQUEST",
  ACTIVATE_PETTY_CASH_TYPE_REQUEST: "ACTIVATE_PETTY_CASH_TYPE_REQUEST",
  getPettyCashTypes: (page, search, filter) => ({
    type: 'GET_PETTY_CASH_TYPES_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createPettyCashType: () => ({
    type: 'CREATE_PETTY_CASH_TYPE_REQUEST'
  }),
  storePettyCashType: data => ({
    type: 'STORE_PETTY_CASH_TYPE_REQUEST',
    payload: { body: data }
  }),
  editPettyCashType: id_petty_cash_type => ({
    type: 'EDIT_PETTY_CASH_TYPE_REQUEST',
    payload: { id_petty_cash_type: id_petty_cash_type }
  }),
  updatePettyCashType: (id_petty_cash_type, data) => ({
    type: 'UPDATE_PETTY_CASH_TYPE_REQUEST',
    payload: {
      id_petty_cash_type: id_petty_cash_type,
      body: data
    }
  }),
  showPettyCashType: id_petty_cash_type => ({
    type: 'SHOW_PETTY_CASH_TYPE_REQUEST',
    payload: { id_petty_cash_type: id_petty_cash_type }
  }),
  deactivatePettyCashType: id_petty_cash_type => ({
    type: 'DEACTIVATE_PETTY_CASH_TYPE_REQUEST',
    payload: { id_petty_cash_type }
  }),
  activatePettyCashType: id_petty_cash_type => ({
    type: 'ACTIVATE_PETTY_CASH_TYPE_REQUEST',
    payload: { id_petty_cash_type }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
